@keyframes unfoldIn {
  0% {
    transform: scaleY(0.002) scaleX(0) translateY(50%);
    opacity: 0; }
  50% {
    transform: scaleY(0.002) scaleX(1) translateY(0%);
    opacity: .1; }
  100% {
    transform: scaleY(1) scaleX(1) translateY(0);
    opacity: 1; } }

@keyframes unfoldOut {
  0% {
    transform: scaleY(1) scaleX(1); }
  50% {
    transform: scaleY(0.005) scaleX(1); }
  100% {
    transform: scaleY(0.005) scaleX(0); } }

@keyframes fadeIn {
  0% {
    background: rgba(0, 0, 0, 0); }
  100% {
    background: rgba(0, 0, 0, 0.7); } }

@keyframes fadeOut {
  0% {
    background: rgba(0, 0, 0, 0.7); }
  100% {
    background: rgba(0, 0, 0, 0); } }
